.postScreen
{
    margin-left: auto;
    margin-right: auto;
}

@media screen and (min-width:800px)
{
    .postScreen
    {
        width: 60%;
    }
}

@media screen and (max-width:800px)
{
    .postScreen
    {
        width: 85%;
    }
}

.PostArea
{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 40px;
    border-bottom: 4px solid black;
    margin-bottom: 30px;
}

.postInfo
{
    color: rgb(255, 255, 255);
    text-shadow: 8px 8px 8px #000;
    position: relative;
}

.postImage, .postImage::before
{
    width: 100%;
    filter: brightness(70%);
    max-height: 550px;
    min-height: 250px;
    height: 100%;
    background-image: url(../../Tools/Images/defaultPostImage.jpg);
}


@media screen and (min-width:840px)
{
    .postInfo
    {
        margin: auto;
        width: 60%;
    }
}

.postHeaderText
{
    overflow-wrap: break-word; 
    word-break: break-word;
    white-space: pre-wrap;
    position: absolute; 
    left: 0;
    right: 0;
    bottom: 0;;;
}

.postTitleX
{
    padding-left: 3%;
    padding-right: 3%;
    font-size: 2.2vh;
}

.postDate
{
    padding-left: 3%;
    padding-right: 3%;
    font-size: 1.6vh;
}

.commentsArea
{
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    
    background-color: rgb(34, 34, 34);
    padding: 4%;
    margin-bottom: 20px;
}

.commentInputLabel
{
    color: white;
}

.nickInput, .commentInput, .emailInput
{
    width: 98.8%;
    margin-top: 6px;
}


.nickInput, .emailInput
{
    height: 20px;
    margin-bottom: 13px;
}

.commentInput
{
    padding-top: 7px;
    padding-bottom: 7px;
    height: 14.1vh;
    margin-bottom: 20px;
    resize: none
}

.commentPostButton
{
    margin-bottom: 7px;
    display: inline-block;
}

.commentRepliesNotifyCheckBoxDiv
{
    margin-left: 15px;
    display: inline-block;
}

.commentRepliesNotifyCheckBox
{
    display: inline-block;
}

input[type=checkbox] 
{
    transform: scale(1.5);
}

.commentRepliesNotifyCheckBoxLabel
{
    margin-left: 3px;
    display: inline-block;
    color: white;
    word-wrap: break-word;
    font-size: 94%;
}

.userCommentArea, .userReplyCommentArea
{
    word-wrap: break-word;
    display: block;
    background-color: rgba(255, 255, 255, 0.973);
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 20px;
    
}

@media screen and (min-width:800px)
{
    .userReplyCommentArea
    {
        width: 86%;
        margin-left: auto;
    }
}

@media screen and (max-width:800px)
{
    .userReplyCommentArea
    {
        width: 74%;
        margin-left: auto;
    }
}

.userCommentNick
{
   padding-top: 10px;
   margin-bottom: -5px;
}

.userCommentDate
{
    color:gray;
    margin-bottom: -5px;
    font-size: 80%;
}

.userCommentContent
{
   margin-bottom: 10px;
   white-space: pre-wrap;
}

.replyLabel
{
   padding-bottom: 20px;
   color:gray;
   font-size: 85%;
   cursor: pointer;
}
